import { ProviderEnvironment } from '@pandora/data-source-utils';
import { offerSelectorGet } from './offerSelectorGet';
import { useAuth } from '@pandora/react-auth-provider';
import { useEnv } from '@pandora/react-env-provider';
import { useFetch } from '@pandora/react-fetch-provider';
import { useMemo } from 'react';
/**
 * Hook to get a high order search function that returns a promise of a DataSourceResponse of type <S>
 * The client is initialised with the context from the following providers:
 * - @pandora/react-fetch-provider
 * - @pandora/react-auth-provider
 * - @pandora/react-env-provider
 *
 * @param dataSource
 */
export const useOfferSelectorGet = (dataSource) => {
    const fetch = useFetch();
    const authData = useAuth();
    const env = ProviderEnvironment[useEnv().toUpperCase()];
    const fetchOptions = { accessToken: authData.getAccessToken(), env, fetch };
    return useMemo(() => offerSelectorGet(fetchOptions, dataSource), [authData, env, fetch]);
};
