import { createLog } from './log';
import { isObject, namespace } from './utilities';

const symbol = Symbol.for(`${namespace}-scaffolds`);
/** @type { Map<string, WeakSet<object>> } */

let repository;
try {
    repository = globalThis[symbol];
    if (!(repository instanceof Map)) {
        repository = new Map();
        globalThis[symbol] = repository;
        repository.set('', new WeakSet());
    }
} catch (error) {
    repository = new Map();
    console.warn(
        `[${namespace}] Unable to share repository of modules:`,
        error
    );
}

/**
 * @type { import('./types').scaffoldModule }
 */
function scaffoldModule(identity) {
    const log = createLog(identity);
    let modules = repository.get(identity);
    if (!modules) {
        modules = new WeakSet();
        repository.set(identity, modules);
    }
    return {
        log,
        createAssert:
            (predicate, message) =>
            (...args) =>
                log.assert(predicate(...args), message, ...args),
        defineModule: (module) => {
            if (module != null) {
                modules.add(module);
                Object.defineProperty(module, Symbol.toStringTag, {
                    configurable: true,
                    value: identity,
                });
            }
            return module;
        },
        isModule: (module) => isObject(module) && modules.has(module),
    };
}

export { scaffoldModule };
