import { scaffoldModule } from './scaffolds';
import {
    isArray,
    isBoolean,
    isFunction,
    isNumber,
    isObject,
    isString,
    omitProperties,
    toArray,
} from './utilities';

const { log, createAssert, defineModule, isModule } =
    scaffoldModule('PlaceholderContext');

const assertOption = createAssert(
    (value) => isFunction(value) || isObject(value),
    'Option must be a function or an object or an array of these:'
);

const assertSerializable = createAssert(
    (value) =>
        value == null ||
        isString(value) ||
        isNumber(value) ||
        isBoolean(value) ||
        (isArray(value) && value.every(assertSerializable)) ||
        (isObject(value) &&
            (isFunction(value.toJSON) ||
                Object.values(value).every(assertSerializable))),
    'Option properties must be serializable to JSON:'
);

/**
 * @template Argument, Options
 * @type { import('./types').createPlaceholderContext<Argument, Options> }
 */
function createPlaceholderContext(options, argument) {
    options = toArray(options).flat(Number.MAX_SAFE_INTEGER);
    const context = {};

    for (let i = options.length - 1; i >= 0; i--) {
        let option = options[i];
        if (isFunction(option)) {
            try {
                option = option(argument, context);
            } catch (error) {
                log.error('Option function error:', error);
                return null;
            }
        }
        if (!assertOption(option)) return null;
        option = omitProperties(option);
        if (!assertSerializable(option)) return null;
        Object.assign(context, option);
    }

    return defineModule(context);
}

/**
 * @type { import('./types').isPlaceholderContext }
 */
const isPlaceholderContext = isModule;

export { createPlaceholderContext, isPlaceholderContext };
