import { buildCheckoutUrl } from '@pandora/commerce-checkout-url-builder';
import {
    price,
    pricePromo,
    priceOptical,
    priceStrikethrough,
    priceAnnual,
} from '@dexter/tacocat-consonant-templates';
import {
    placeholderStages,
    createPlaceholderTemplate,
} from '@dexter/tacocat-core';

const { pending, rejected, resolved } = placeholderStages;

const wcsPendingHtmlPlaceholderTemplate = createPlaceholderTemplate(
    () => true,
    () => '',
    pending
);

const wcsRejectedHtmlPlaceholderTemplate = createPlaceholderTemplate(
    () => true,
    () => '...',
    rejected
);

// if no market segment provided in placeholder context,
// take first available market segment from the WCS payload
const getMarketSegment = (contextMarketSegment, wcsMarketSegments) =>
    contextMarketSegment ?? wcsMarketSegments?.[0] ?? '';

/**
 * @param {import('@dexter/tacocat-core').PlaceholderContext} hostContext
 * @param {import('@dexter/tacocat-core').PlaceholderValue} value
 */
function formatCheckoutLinkHref(hostContext, value) {
    const {
        workflow,
        workflowStep,
        clientId,
        context,
        country,
        imsCountry,
        env,
        language,
        marketSegment,
        countrySpecific,
        quantity,
        authCode,
        promotionCode,
        customParameters,
        landscape,
    } = hostContext;
    const { offerId, offerType, productArrangementCode, marketSegments } = value;
    const href = buildCheckoutUrl(workflow, {
        clientId,
        country: imsCountry || country,
        authCode,
        context,
        env,
        landscape,
        items: [
            {
                id: offerId,
                countrySpecific,
                quantity,
            },
        ],
        language,
        workflowStep,
        marketSegment: getMarketSegment(marketSegment, marketSegments),
        offerType,
        productArrangementCode,
        checkoutPromoCode: promotionCode,
        ...customParameters,
    });
    return { href };
}
const wcsCheckoutHrefPlaceholderTemplate = createPlaceholderTemplate(
    (context) => context.type === 'checkoutUrl',
    formatCheckoutLinkHref,
    resolved
);

/**
 * role of that template is either to use striketrough rendering for normal promos,
 * or prices rendering for normal regular prices.
 */
const wcsPriceHtmlPlaceholderTemplate = createPlaceholderTemplate(
    (context) => context.type == 'price',
    (context, value) =>
        context.promotionCode
            ? pricePromo(
                  context,
                  {
                      ...value,
                      ...value.priceDetails,
                  },
                  { 'data-analytics-productinfo': `[${value.analytics}]` }
              )
            : price(
                  context,
                  {
                      ...value,
                      ...value.priceDetails,
                  },
                  { 'data-analytics-productinfo': `[${value.analytics}]` }
              ),
    resolved
);

const [
    wcsPriceOpticalHtmlPlaceholderTemplate,
    wcsPriceStrikethroughHtmlPlaceholderTemplate,
    wcsPriceAnnualHtmlPlaceholderTemplate,
] = Object.entries({
    priceOptical,
    priceStrikethrough,
    priceAnnual,
}).map(([type, template]) =>
    createPlaceholderTemplate(
        (context) => context.type === type,
        (
            context,
            {
                analytics,
                commitment,
                priceDetails: {
                    formatString,
                    price,
                    priceWithoutDiscount,
                    taxDisplay,
                    taxTerm,
                    usePrecision,
                },
                term,
            }
        ) =>
            template(
                context,
                {
                    commitment,
                    formatString,
                    price,
                    priceWithoutDiscount,
                    taxDisplay,
                    taxTerm,
                    usePrecision,
                    term,
                },
                { 'data-analytics-productinfo': `[${analytics}]` }
            ),
        resolved
    )
);

export {
    wcsPendingHtmlPlaceholderTemplate,
    wcsRejectedHtmlPlaceholderTemplate,
    wcsCheckoutHrefPlaceholderTemplate,
    wcsPriceHtmlPlaceholderTemplate,
    wcsPriceOpticalHtmlPlaceholderTemplate,
    wcsPriceStrikethroughHtmlPlaceholderTemplate,
    wcsPriceAnnualHtmlPlaceholderTemplate,
    getMarketSegment,
};
