import { createDeferral } from './deferral';
import {
    createPlaceholderContext,
    isPlaceholderContext,
} from './placeholderContext';
import { isPlaceholderProvider } from './placeholderProvider';
import { createPlaceholderState } from './placeholderState';
import { placeholderStages } from './placeholderStages';
import { isPlaceholderTemplate } from './placeholderTemplate';
import { scaffoldModule } from './scaffolds';

/**
 * @typedef { import('./types').PlaceholderContext<unknown> } PlaceholderContext
 * @typedef { import('./types').PlaceholderProvider<unknown, unknown> } PlaceholderProvider
 * @typedef { import('./types').placeholderStage } placeholderStage
 * @typedef { import('./types').PlaceholderState } PlaceholderState
 * @typedef { import('./types').PlaceholderTemplate<unknown, unknown> } PlaceholderTemplate
 */

const { pending, rejected, resolved } = placeholderStages;

const { log, createAssert, defineModule, isModule } =
    scaffoldModule('PlaceholderEngine');

const assertContextArgument = createAssert(
    isPlaceholderContext,
    'Argument "context" must be an instance of PlaceholderContext:'
);
const assertModuleArgument = createAssert(
    (value) => isPlaceholderProvider(value) || isPlaceholderTemplate(value),
    'Argument "module" must be an instance of PlaceholderProvider or PlaceholderTemplate type:'
);

/**
 * @type { import('./types').createPlaceholderEngine }
 */
function createPlaceholderEngine() {
    const engine = defineModule({
        dispose,
        provide,
        register,
        resolve,
    });

    const groupDeferral = createDeferral(group);
    /**
     * @type { Set<PlaceholderProvider> }
     */
    const providers = new Set();
    /**
     * @type { Map<PlaceholderProvider, Map<PlaceholderContext, PlaceholderState>> }
     */
    const providerQueues = new Map();
    /**
     * @type { Map<string, PlaceholderState> }
     */
    const providedStates = new Map();
    /**
     * @type { Map<string, PlaceholderState> }
     */
    const resolvedStates = new Map();
    /**
     * @type { { [stage in placeholderStage]: Set<PlaceholderTemplate> } }
     */
    const templates = {
        [pending]: new Set(),
        [resolved]: new Set(),
        [rejected]: new Set(),
    };

    log.debug('Created');

    function dispose() {
        groupDeferral.dispose();
        providers.clear();
        providerQueues.clear();
        providedStates.clear();
        templates[pending].clear();
        templates[resolved].clear();
        templates[rejected].clear();
        resolvedStates.clear();
        log.debug('Disposed');
    }

    /**
     * @param { [PlaceholderProvider, PlaceholderContext, PlaceholderState][] } collected
     */
    function group(collected) {
        /**
         * @type { Map<PlaceholderProvider, Map<PlaceholderContext, PlaceholderState>> }
         */
        const groups = new Map();
        for (const [provider, providerContext, providerState] of collected) {
            if (!groups.has(provider)) {
                groups.set(provider, new Map());
            }
            groups.get(provider).set(providerContext, providerState);
        }

        groups.forEach(procure);
    }

    /**
     * @param { Map<PlaceholderContext, PlaceholderState> } grouped
     * @param { PlaceholderProvider } provider
     */
    async function procure(grouped, provider) {
        let contexts = [...grouped.keys()];
        contexts = [...contexts];
        await Promise.all(
            provider.provide(contexts).map((promise) =>
                promise.then((results) =>
                    results.forEach(({ context, value }) => {
                        let providerState = grouped.get(context);
                        if (providerState) {
                            grouped.delete(context);
                        } else {
                            const key = JSON.stringify(context);
                            providerState = providedStates.get(key);
                            if (!providerState) {
                                providerState = createPlaceholderState(context);
                                providedStates.set(key, providerState);
                            }
                        }
                        providerState.update(resolved, value);
                    })
                )
            )
        );
        for (const [context, state] of grouped.entries()) {
            state.update(rejected, undefined);
            log.error('Not provided:', { context });
        }
    }

    /**
     * @param { PlaceholderContext } context
     * @returns { PlaceholderState }
     */
    function provide(context) {
        const requestedContext = isPlaceholderContext(context)
            ? context
            : createPlaceholderContext(context);
        if (!assertContextArgument(requestedContext)) return null;

        const requestContextKey = JSON.stringify(requestedContext);
        let providedState = providedStates.get(requestContextKey);

        if (!providedState) {
            let provider, providedContext;
            for (provider of providers) {
                providedContext = provider.identify(requestedContext);
                if (providedContext != null) break;
            }

            if (providedContext == null) {
                log.error('Provider not found:', { context: requestedContext });
                return {
                    get promise() {
                        return Promise.reject({ context: requestedContext });
                    },
                    subscribe(observer) {
                        observer(rejected, undefined);
                        return () => {};
                    },
                    unsubscribe() {},
                };
            }

            const providedContextKey = JSON.stringify(providedContext);
            providedState = providedStates.get(providedContextKey);

            if (!providedState) {
                providedState = createPlaceholderState(providedContext);
                providedStates.set(providedContextKey, providedState);
                groupDeferral.schedule(
                    provider,
                    providedContext,
                    providedState
                );
            }
        }

        return providedState;
    }

    /**
     * @param  { ...(PlaceholderProvider | PlaceholderTemplate) } modules
     */
    function register(...modules) {
        for (const module of modules.flat(2)) {
            if (!assertModuleArgument(module)) return;
            log.debug('Registering:', { module });
            if (isPlaceholderTemplate(module)) {
                templates[module.stage].add(module);
            } else if (isPlaceholderProvider(module)) {
                providers.add(module);
            }
        }
        return engine;
    }

    /**
     * @param { PlaceholderContext } context
     */
    function resolve(context) {
        const requestedContext = isPlaceholderContext(context)
            ? context
            : createPlaceholderContext(context);
        if (!assertContextArgument(requestedContext)) return null;

        const requestedContextKey = JSON.stringify(requestedContext);
        let resolvedState = resolvedStates.get(requestedContextKey);

        if (!resolvedState) {
            const providedState = provide(requestedContext);
            resolvedState = createPlaceholderState(requestedContext);
            resolvedStates.set(requestedContextKey, resolvedState);

            providedState.subscribe(function format(stage, value) {
                const formattedContext = createPlaceholderContext({
                    ...resolvedState.context,
                    ...providedState.context,
                });
                const template = [...templates[stage]].find((candidate) =>
                    candidate.accept(formattedContext, value)
                );
                if (template) {
                    try {
                        resolvedState.update(stage, template.format(formattedContext, value));
                    } catch {
                        // ignore
                    }
                } else {
                    if (stage === resolved) {
                        log.error('Template not found:', { context: formattedContext });
                    }
                    resolvedState.update(stage, value);
                }
            });
        }

        return resolvedState;
    }

    return engine;
}

/**
 * @type { import('./types').isPlaceholderEngine }
 */
const isPlaceholderEngine = isModule;

export { createPlaceholderEngine, isPlaceholderEngine };
