/**
 * @type { import('./types').isElement }
 */
const isElement = (value) => value != null && value instanceof HTMLElement;
/**
 * @type { import('./types').isNode }
 */
const isNode = (value) => value != null && value instanceof Node;
/**
 * @type { import('./types').isText }
 */
const isText = (value) => value != null && value instanceof Text;

export { isElement, isNode, isText };
