import { isPlaceholderContext } from './placeholderContext';
import { isPlaceholderStage, placeholderStages } from './placeholderStages';
import { scaffoldModule } from './scaffolds';
import { isFunction } from './utilities';

const { pending } = placeholderStages;

const { log, createAssert, defineModule, isModule } =
    scaffoldModule('PlaceholderState');

const assertContextArgument = createAssert(
    isPlaceholderContext,
    'Argument "context" must be an instance of PlaceholderContext type:'
);
const assertObserverArgument = createAssert(
    isFunction,
    'Argument "observer" must be a function:'
);
const assertStageArgument = createAssert(
    isPlaceholderStage,
    'Argument "stage" must be a member of placeholderStages enum:'
);

/**
 * @type { import('./types').createPlaceholderState }
 */
function createPlaceholderState(context) {
    if (!assertContextArgument(context)) {
        return null;
    }

    const current = { stage: pending };
    const observers = new Set();

    function notify(observer) {
        try {
            observer(current.stage, current.value);
        } catch (error) {
            log.error(
                'Failed to notify observer:',
                { context, observer },
                error
            );
        }
    }

    function subscribe(observer) {
        if (assertObserverArgument(observer) && !observers.has(observer)) {
            observers.add(observer);
            notify(observer);
            return () => unsubscribe(observer);
        }
        return null;
    }

    function unsubscribe(observer) {
        return observers.delete(observer);
    }

    function update(stage, value) {
        if (assertStageArgument(stage)) {
            if (current.stage !== stage || current.value !== value) {
                current.stage = stage;
                current.value = value;
                observers.forEach(notify);
            }
        }
    }

    return defineModule({
        get context() {
            return context;
        },
        get promise() {
            return new Promise((resolve, reject) => {
                const observer = (stage, value) => {
                    if (stage === placeholderStages.rejected) {
                        reject({ context });
                    } else if (stage === placeholderStages.resolved) {
                        resolve({ context, value });
                    } else return;
                    observers.delete(observer);
                };
                if (current.stage === placeholderStages.pending) {
                    observers.add(observer);
                } else {
                    observer(current.stage, current.value);
                }
            });
        },
        subscribe,
        unsubscribe,
        update,
    });
}

/**
 * @type { import('./types').isPlaceholderState }
 */
const isPlaceholderState = isModule;

export { createPlaceholderState, isPlaceholderState };
