import {
    isFunction,
    isPositiveFiniteNumber,
    omitProperties,
    scaffoldModule,
} from '@dexter/tacocat-core';
import { Environment, ProviderEnvironment } from '@pandora/data-source-utils';
import { webCommerceArtifact } from '@pandora/data-source-wcs';

const { log, createAssert, defineModule, isModule } =
    scaffoldModule('WcsClient');

const assertFetchOption = createAssert(
    isFunction,
    'Option "fetch" must be a function, if provided:'
);
const assertOfferSelectorLimitOption = createAssert(
    isPositiveFiniteNumber,
    'Option "offerSelectorLimit" must be a positive finite number, if provided:'
);
const assertRetryOption = createAssert(
    isFunction,
    'Option "retry" must be a function, if provided:'
);

const getWcsClientBaseUrl = (env) =>
    env === ProviderEnvironment.STAGE
        ? 'https://wcs.stage.adobe.com'
        : 'https://wcs.adobe.com';

/** @type { import('./types').createWcsClient } */
function createWcsClient({
    accessToken,
    apiKey,
    env = ProviderEnvironment.PRODUCTION,
    baseUrl = getWcsClientBaseUrl(env),
    environment = Environment.PRODUCTION,
    fetch = window.fetch.bind(window),
    landscape,
    offerSelectorLimit = 25,
    retry = (action) => action(),
} = {}) {
    if (
        !assertFetchOption(fetch) ||
        !assertOfferSelectorLimitOption(offerSelectorLimit) ||
        !assertRetryOption(retry)
    ) {
        return null;
    }

    if (env === ProviderEnvironment.STAGE) {
        /**
         * For stage env we don't want to pass `landscape` param anymore
         * so to use default WCS dataplane.
         * @see https://developers.corp.adobe.com/aos/docs/guide/overview.md/#dataplanes
         * But it is defaulted by pandora library to `PUBLISHED` if not provided.
         * @see https://git.corp.adobe.com/PandoraUI/odm-core/blob/master/packages/data-source-aos/src/urlBuilder.ts#L63
         * Passing non-existing value for now, stage env just ignores it.
         * TODO: update pandora to allow unset landscape
         * {@link https://jira.corp.adobe.com/browse/MWPW-133880}
         */
        landscape = 'ALL';
    }
    const fetchOptions = omitProperties({
        accessToken,
        apiKey,
        baseUrl,
        env,
        fetch,
    });
    const queryOptions = omitProperties({
        apiKey,
        environment,
        landscape,
    });
    const options = omitProperties({
        baseUrl,
        env,
        environment,
        landscape,
        offerSelectorLimit,
    });

    log.debug('Created:', { options });

    const getWebCommerceArtifactPromise = webCommerceArtifact(fetchOptions);
    return defineModule({
        getWebCommerceArtifact(options) {
            return retry(() =>
                getWebCommerceArtifactPromise(options, queryOptions)
            );
        },
        options,
    });
}

const isWcsClient = isModule;

export { createWcsClient, getWcsClientBaseUrl, isWcsClient };
