/**
 * Build a paginated response given items and the response from the server.
 *
 * @param items returned by the server. This is the data to be used by a client.
 * @param response containing any metadata for building the paginated response.
 */
export const buildPaginatedResponse = (items, response) => {
    return {
        data: items,
        totalResultCount: Number(response.headers.get('x-total-count')),
        pageSize: Number(response.headers.get('x-page-size')),
        pageCount: Number(response.headers.get('x-page-count')),
        currentPage: Number(response.headers.get('x-current-page')),
    };
};
