import { isBoolean, isFunction, scaffoldModule } from '@dexter/tacocat-core';
import { isNode } from './domUtilities';

const { log, createAssert, defineModule, isModule } = scaffoldModule(
    'DomPlaceholderRenderer'
);

const assertAcceptArgument = createAssert(
    isFunction,
    'Argument "accept" must be a function:'
);

const assertRenderArgument = createAssert(
    isFunction,
    'Argument "render" must be a function:'
);
const assertRenderReturn = createAssert(
    (value) => isBoolean(value) || isNode(value),
    'Argument "render" must return a boolean or an instance of DOM node:'
);

function createDomPlaceholderRenderer(accept, render) {
    if (!assertAcceptArgument(accept) || !assertRenderArgument(render))
        return null;
    return defineModule({
        accept(node, value) {
            if (value == null) return false;
            try {
                const result = accept(node, value);
                if (isBoolean(result) || (isNode(result) && node.isConnected)) {
                    return result === node ? true : result;
                }
                log.error(
                    'If returns, render function must return either new DOM Node, connected to the DOM tree, boolean, null or undefined:',
                    { result }
                );
            } catch (error) {
                log.error('Accept function error:', error);
            }
            return false;
        },
        render(node, stage, value) {
            if (value == null) return false;
            try {
                const result = render(node, stage, value);
                if (!assertRenderReturn(result)) return false;
                if (result === true) {
                    log.debug('Rendered:', { node, stage, value });
                } else if (result === false) {
                    log.debug('Not rendered:', { node, stage, value });
                } else {
                    const newNode = result;
                    log.debug('Replaced:', { node, newNode, stage, value });
                }
                return result;
            } catch (error) {
                log.error('Render function error:', error);
            }
            return false;
        },
    });
}

const isDomPlaceholderRenderer = isModule;

export { createDomPlaceholderRenderer, isDomPlaceholderRenderer };
