import { placeholderStages } from '@dexter/tacocat-core';
import { isElement } from './domUtilities';

const { pending, rejected, resolved } = placeholderStages;

const domPlaceholderCssClassName = 'placeholder';
const domPlaceholderCssClassNamesByStage = {
    [pending]: `${domPlaceholderCssClassName}-${pending}`,
    rejected: `${domPlaceholderCssClassName}-${rejected}`,
    resolved: `${domPlaceholderCssClassName}-${resolved}`,
};

/**
 * If node is a DOM Element, updates its CSS class list with CSS class names related to the provided placeholder `stage`.
 * CSS class names are defined by the `domPlaceholderCssClassName` constant
 * and `domPlaceholderCssClassNamesByStage` keyed collection, exported by this module.
 * @param { any } node
 * @param { import('./types').placeholderStage } stage
 */
function updateDomPlaceholderCssClassNames(node, stage) {
    if (isElement(node)) {
        node.classList.remove(
            domPlaceholderCssClassNamesByStage.pending,
            domPlaceholderCssClassNamesByStage.rejected,
            domPlaceholderCssClassNamesByStage.resolved
        );
        node.classList.add(
            domPlaceholderCssClassName,
            domPlaceholderCssClassNamesByStage[stage]
        );
    }
}

export {
    domPlaceholderCssClassName,
    domPlaceholderCssClassNamesByStage,
    updateDomPlaceholderCssClassNames,
};
