/**
 * @type { import("./types").placeholderStages }
 */
const placeholderStages = Object.freeze({
    pending: 'pending',
    rejected: 'rejected',
    resolved: 'resolved',
});

const placeholderStageValues = Object.values(placeholderStages);

/**
 * @type { import("./types").isPlaceholderStage }
 */
const isPlaceholderStage = (value) => placeholderStageValues.includes(value);

export { isPlaceholderStage, placeholderStages };
