import { createDomElementPlaceholderRenderer } from '@dexter/tacocat-dom-placeholders';
import {
    createPlaceholderContext,
    isPlaceholderEngine,
    scaffoldModule,
    isArray,
} from '@dexter/tacocat-core';
import { useCallback, useContext, useEffect, useState } from 'react';
import { PlaceholderContext } from './reactContext';

const { log, createAssert } = scaffoldModule('usePlaceholder');

const assertReactContext = createAssert(
    (reactContext) =>
        isPlaceholderEngine(reactContext.engine) &&
        isArray(reactContext.globals),
    'Need "PlaceholderContext" initialised with placeholder engine and array of globals:'
);

const render = createDomElementPlaceholderRenderer();

/**
 * @type { import('./types').usePlaceholder }
 * React hook for consuming a placeholder key provided by Tacocat.
 * Internally it uses the context api to retrieve a placeholder key parser, placeholder store, placeholder view.
 * @param options
 * Arbitrary objects or functions accepting placeholder key and returning objects extending context of placeholder.
 */
function usePlaceholder(...options) {
    const reactContext = useContext(PlaceholderContext) ?? {};

    const [node, setNode] = useState();

    const ref = useCallback((node) => {
        if (node == null) return;
        setNode(node);
    }, []);

    useEffect(() => {
        if (!node) return;
        const datasetContext = { ...node.dataset };
        if (!assertReactContext(reactContext)) {
            return;
        }
        const { engine, globals } = reactContext;
        const context = createPlaceholderContext([
            ...options,
            datasetContext,
            globals,
        ]);
        log.debug('Extracted:', { context, node });
        return engine.resolve(context).subscribe((stage, value) => {
            render.render(node, stage, value);
        });
    }, [node, options]);

    return ref;
}

function usePlaceholderValue(...options) {
    const reactContext = useContext(PlaceholderContext) ?? {};

    const [result, setResult] = useState();

    useEffect(() => {
        if (!assertReactContext(reactContext)) {
            return;
        }
        const { engine, globals } = reactContext;
        const context = createPlaceholderContext([...options, globals]);
        return engine.resolve(context).subscribe((stage, value) => {
            setResult({ stage, value });
        });
    }, [options]);

    return result;
}

export { usePlaceholder, usePlaceholderValue };
