import { isFunction, isObject } from '@dexter/tacocat-core';
import { updateDomPlaceholderCssClassNames } from './domPlaceholderCssClassNames';
import { createDomPlaceholderRenderer } from './domPlaceholderRenderer';
import { isElement } from './domUtilities';

const acceptedTypes = [
    'boolean',
    'bigint',
    'function',
    'number',
    'object',
    'string',
];

/** @type { import('./types').createDomElementPlaceholderRenderer } */
const createDomElementPlaceholderRenderer = () =>
    createDomPlaceholderRenderer(
        (node, value) =>
            isElement(node) && acceptedTypes.includes(typeof value),
        (node, stage, value) => {
            if (isFunction(value)) {
                const result = value(node, stage) ?? true;
                updateDomPlaceholderCssClassNames(
                    isElement(result) ? result : node,
                    stage
                );
                return result;
            }
            if (isObject(value)) {
                for (const name of Object.keys(value)) {
                    if (name) node.setAttribute(name, value[name]);
                    // TODO: design more semantic format, maybe similar to ReactJS
                    else node.innerHTML = value[name];
                }
                updateDomPlaceholderCssClassNames(node, stage);
                return true;
            }
            node.innerHTML = value;
            updateDomPlaceholderCssClassNames(node, stage);
            return true;
        }
    );

export { createDomElementPlaceholderRenderer };
