import { scaffoldModule } from './scaffolds';
import { isPlaceholderStage } from './placeholderStages';
import { isFunction } from './utilities';

const { log, createAssert, defineModule, isModule } = scaffoldModule(
    'PlaceholderTemplate'
);

const assertAcceptArgument = createAssert(
    isFunction,
    'Argument "accept" must be a function:'
);
const assertFormatArgument = createAssert(
    isFunction,
    'Argument "format" must be a function:'
);
const assertStageArgument = createAssert(
    isPlaceholderStage,
    'Argument "stage" must be member of placeholderStages enum:'
);

/**
 * @template Options, Value, Markup
 * @type { import('./types').createPlaceholderTemplate<Options, Value, Markup> }
 */
function createPlaceholderTemplate(accept, format, stage) {
    if (
        !assertAcceptArgument(accept) ||
        !assertFormatArgument(format) ||
        !assertStageArgument(stage)
    ) {
        return null;
    }
    return defineModule({
        accept(context, value) {
            try {
                return accept(context, value);
            } catch (error) {
                log.error('Accept function error:', { context, value }, error);
                return false;
            }
        },
        format(context, value) {
            try {
                const markup = format(context, value);
                log.debug('Formatted:', { context, stage, value, markup });
                return markup;
            } catch (error) {
                log.error('Format function error:', { context, value }, error);
                throw error;
            }
        },
        stage,
    });
}

/**
 * @type { import('./types').isPlaceholderTemplate }
 */
const isPlaceholderTemplate = isModule;

export { createPlaceholderTemplate, isPlaceholderTemplate };
