import { scaffoldModule } from './scaffolds';
import { isFunction } from './utilities';

const { log, createAssert, defineModule } = scaffoldModule('Deferral');

const assertMethodArgument = createAssert(
    isFunction,
    'Argument "method" must be a function:'
);

/**
 * @template Arguments
 * @type { import('./types').createDeferral<Arguments> }
 */
function createDeferral(method) {
    if (!assertMethodArgument(method)) {
        return null;
    }

    const scheduled = [];
    let timer = 0;

    function dispose() {
        if (timer) {
            clearTimeout(timer);
            timer = 0;
        }
        scheduled.length = 0;
    }

    function schedule(...args) {
        scheduled.push(args);
        if (!timer) {
            timer = setTimeout(() => {
                timer = 0;
                const collected = [...scheduled];
                scheduled.length = 0;
                try {
                    method(collected);
                } catch (error) {
                    log.error('Deferred function error:', error);
                }
            });
        }
    }

    return defineModule({ dispose, schedule });
}

export { createDeferral };
