import {
    createPlaceholderContext,
    createPlaceholderProvider,
    isNotEmptyString,
    omitProperties,
    scaffoldModule,
    forceTaxExclusivePrice,
} from '@dexter/tacocat-core';
import { isWcsClient } from './wcsClient';

const { log, createAssert, defineModule } = scaffoldModule(
    'WcsPlaceholderProvider'
);

const assertClientArgument = createAssert(
    isWcsClient,
    'Argument "client" must be an instance of WcsClient:'
);

/**
 * @type { import('./types').createWcsPlaceholderProvider }
 */
function createWcsPlaceholderProvider(client, ...globals) {
    if (!assertClientArgument(client)) return null;

    log.debug('Created:', { client, globals });

    /**
     * @param { import('./types').WcsPlaceholderContext } param0
     */
    function identify({
        country,
        currency,
        language,
        isPerpetual,
        perpetualLanguage,
        offerSelectorId,
        promotionCode,
        forceTaxExclusive,
    }) {
        if (isNotEmptyString(country) && isNotEmptyString(offerSelectorId)) {
            const { env, landscape } = client.options;
            return {
                country: country.toUpperCase(),
                currency,
                env,
                landscape,
                language: isNotEmptyString(language)
                    ? language.toLowerCase()
                    : 'en',
                isPerpetual,
                perpetualLanguage: perpetualLanguage ?? 'EN',
                offerSelectorId,
                promotionCode,
                forceTaxExclusive: forceTaxExclusive ?? false,
            };
        }
        return null;
    }

    /**
     * if country is GB we need to get both EN and MULT offers. EN is taken if exists, MULT used as a backup.
     * for any other country, if it's not a perpetual offer we use language=MULT.
     * for a perpetual offer of any country we request all possible languages. Then preferred language (default - 'EN') is taken.
     * @param {*} country country code. 'GB', 'US' etc.
     * @param {*} isPerpetual if this placeholder is for a perpetual offer. data-perpetual='true'.
     * @param {*} perpetualLanguage the language of the perpetual product. 'MULT', 'EN', 'JA' etc. Not a part of the 'locale'!
     */
    function getLanguage(country, isPerpetual, perpetualLanguage) {
        if (isPerpetual) {
            return perpetualLanguage;
        }
        return country === 'GB' ? undefined : 'MULT';
    }
    /**
     * @param { import('./types').WcsPlaceholderContext[] } contexts
     */
    function provide(contexts) {
        const groups = [];
        for (const context of contexts) {
            const {
                country,
                currency,
                language,
                isPerpetual,
                perpetualLanguage,
                offerSelectorId,
                promotionCode,
                forceTaxExclusive,
            } = context;
            if (
                !isNotEmptyString(country) ||
                !isNotEmptyString(language) ||
                !isNotEmptyString(offerSelectorId)
            ) {
                log.error('Unknown context:', { context });
                continue;
            }
            const languageParameter = getLanguage(
                country,
                isPerpetual,
                perpetualLanguage
            );

            const locale = `${language.toLowerCase()}_${country.toUpperCase()}`;
            const group = groups.find(
                ({ options }) =>
                    country === options.country &&
                    currency === options.currency &&
                    locale === options.locale &&
                    promotionCode === options.promotionCode &&
                    languageParameter === options.language &&
                    options.offerSelectorIds.length <
                        client.options.offerSelectorLimit
            );
            if (group) {
                group.options.offerSelectorIds.push(offerSelectorId);
                group.contexts.push(context);
            } else {
                groups.push({
                    contexts: [context],
                    options: omitProperties({
                        country,
                        currency,
                        locale,
                        promotionCode,
                        language: languageParameter,
                        offerSelectorIds: [offerSelectorId],
                        forceTaxExclusive,
                    }),
                });
            }
        }
        return groups.map(({ contexts, options }) => {
            log.debug('Fetching:', { options });
            return client.getWebCommerceArtifact(options).then(({ data }) => {
                log.debug('Fetched:', { data, options });
                let flattenedOffers = data.resolvedOffers.flatMap((value) =>
                    value.offerSelectorIds.map((offerSelectorId) => {
                        let context = contexts.find(
                            (context) =>
                                context.offerSelectorId === offerSelectorId
                        );
                        if (!context) {
                            context = { ...options, offerSelectorId };
                            delete context.offerSelectorIds;
                            context = createPlaceholderContext(context);
                        }
                        return { context, value };
                    })
                );

                flattenedOffers = flattenedOffers.map(({ context, value }) => {
                    if (context.forceTaxExclusive === true) {
                        value = forceTaxExclusivePrice(value);
                    }
                    return { context, value };
                });

                if (options.country === 'GB') {
                    // filter flatten offers and pick EN ones over MULT
                    flattenedOffers = Array.from(
                        flattenedOffers
                            .reduce(
                                (acc, { context, value }) =>
                                    !acc.has(context.offerSelectorId) ||
                                    acc.get(context.offerSelectorId).value
                                        .language === 'MULT'
                                        ? acc.set(context.offerSelectorId, {
                                              context,
                                              value,
                                          })
                                        : acc,
                                new Map()
                            )
                            .values()
                    );
                }
                return flattenedOffers;
            });
        });
    }

    return defineModule(
        createPlaceholderProvider(identify, provide, ...globals)
    );
}

export { createWcsPlaceholderProvider };
